<template>
    <div class="mainIEP">
        <div class="discuss-content">

            <div class="discuss-list">
                <div class="toolBox">
                    <div class="addWrap">
                        <el-button @click="addDiscussReport"></el-button>
                    </div>
                </div>

                <div class="center-content">
                    <el-card class="cardBox colorHeader " :body-style="{ padding: '0px'}">
                        <div class="tableWrap ">
                            <el-table :data="discussReports" style="width: 100%">
                                <el-table-column type="index" width="160" style="">
                                </el-table-column>

                                <el-table-column prop="Name" label="" width="600">
                                </el-table-column>

                                <el-table-column label="">
                                    <template scope="scope">
                                        <!--<el-button size="small" type="danger" @click="assessmentDetail(scope.$index, scope.row)"></el-button>-->
                                        <el-button size="small" :plain="true" type="info" @click="viewDiscussReportDetails(scope.$index,scope.row)"></el-button>
                                        <el-button size="small" :plain="true" type="danger" @click="deleteDiscussReport(scope.$index, scope.row)"></el-button>
                                    </template>
                                </el-table-column>

                            </el-table>
                        </div>
                        <el-pagination layout="prev, pager, next" @current-change="pageChange" :page-size="pageSize" :total="recordsCount" style="float:right;"></el-pagination>
                    </el-card>
                </div>
            </div>

            <el-dialog :title="discussDialogTitle" v-model="showDetailsFlag" size="small">

                <el-form label-width="110px" :model="discussReport" >
                    <el-row :gutter="24">
                        <el-col :span="24" class="content">
                            <el-form-item label=":">
                                <el-input v-model="discussReport.Name" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="24" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.Problem">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.Measure">
                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.Process">
                                </el-input>
                            </el-form-item>
                        </el-col>

                    </el-row>

                    <el-row :gutter="24">

                        <el-col :span="12" class="content">

                            <el-form-item label=":">
                                <el-select v-model="currentGroup" placeholder="" style="width:100%" @change="groupChange">
                                    <el-option v-for="(item, index) in patientGroups" :key="item.groupID" :label=item.groupName :value=item.groupID>
                                    </el-option>
                                </el-select>
                            </el-form-item>

                        </el-col>

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-select v-model="currentPatient" placeholder="" style="width:100%">
                                    <el-option v-for="(item, index) in patients" :key="item.ID" :label=item.Name :value=item.ID>
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-date-picker type="date" placeholder="" v-model="discussReport.DiscussDate" style="width:100%"></el-date-picker>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input v-model="discussReport.Recorder" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.Suggestion">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.FeedBack">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="discussReport.AbilityLevel">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-form>

                <div style="float:right;margin-bottom:10px;margin-right:25px">
                    <el-button @click="cancel"></el-button>
                    <el-button type="primary" @click="saveDiscussReport"></el-button>
                </div>

            </el-dialog>

        </div>

    </div>
</template>
<script>
import api from '../../../api/index.js';
export default {
    name: 'iepStudy',
    components: {
    },
    data() {
        return {
            showDetailsFlag: false,
            pageIndex: 1,
            recordsCount: 1,
            pageSize: 10,
            discussReports: [],
            discussReport: {
                ID: '',
                Name: '',
                StudentID: '',
                Measure: '',//
                FeedBack: '',// 
                Recorder: '',//
                DiscussDate: '',//
                AbilityLevel: '',//
                Problem: '',//
                Process: '',//
                Suggestion: ''//
            },
            discussDialogTitle: '',
            currentGroup: '',//
            patientGroups: [],//
            patients: [],//
            currentPatient: '',
        }
    },
    methods: {
        initData() {
            this.patients = [];
            this.currentGroup = '';
            this.currentPatient = '';
            this.patientGroups = this.$store.state.mystudent.patientList;
            this.discussReport = {
                ID: '',
                Name: '',
                StudentID: '',
                Measure: '',//
                FeedBack: '',// 
                Recorder: '',//
                DiscussDate: '',
                AbilityLevel: '',//
                Problem: '',//
                Process: '',//
                Suggestion: ''//
            };
            this.discussReport.DiscussDate = new Date();
        },
        pageChange(currentPage) {
            this.pageIndex = currentPage;
            this.requestDiscussReports();
        },
        groupChange(id) {
            let _this = this;
            for (let i = 0; i < _this.patientGroups.length; i++) {
                if (_this.patientGroups[i].groupID === id) {
                    _this.patients = _this.patientGroups[i].groupPatients;
                    break;
                }
            }
        },
        viewDiscussReportDetails(index, row) {
            this.initData();
            this.discussReport = this.discussReports[index];
            this.showDetailsFlag = true;
            this.discussDialogTitle = '';
            let isFind = false;
            // 
            for (let i = 0; i < this.patientGroups.length; i++) {
                for (let j = 0; j < this.patientGroups[i].groupPatients.length; j++) {
                    if (this.patientGroups[i].groupPatients[j].ID === this.discussReport.StudentID) {
                        this.currentGroup = this.patientGroups[i].groupID;
                        this.patients = this.patientGroups[i].groupPatients;
                        isFind = true;
                        break;
                    }

                }
                if (isFind) {
                    break;
                }

            }
            this.currentPatient = this.discussReports[index].StudentID;
        },
        deleteDiscussReport(index, row) {
            let _this = this;
            _this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let params = { id: _this.discussReports[index].ID };
                api.removeDiscussReport(params).then(res => {

                    if (res.Status) {
                        _this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    _this.requestDiscussReports();
                }).catch(err => {
                    _this.$message({
                        type: 'success',
                        message: ','
                    });
                });
            }).catch(() => {

            });

        },

        //dialog
        addDiscussReport() {
            this.showDetailsFlag = true;
            this.initData();
            this.discussDialogTitle = '';
            if (this.patientGroups.length > 0) {
                this.currentGroup = this.patientGroups[0].groupID;
                if (this.patientGroups[0].groupPatients.length > 0) {
                    this.patients = this.patientGroups[0].groupPatients;
                }
            }
        },

        //
        requestDiscussReports() {
            let _this = this;
            let params = { PageIndex: _this.pageIndex, PageSize: _this.pageSize };
            api.getDiscussReportList(params).then(res => {
                _this.recordsCount = res.Total;
                _this.discussReports = res.Records;
            }).catch(err => {
                _this.$message.error('');
            });
        },

        //
        saveDiscussReport() {
            let _this = this;
            if (_this.discussReport.Name === "") {
                _this.$message.warning('');
                return;
            }
            if (_this.currentPatient === '') {
                _this.$message.warning('');
                return;
            }
            _this.discussReport.StudentID = _this.currentPatient;
            api.saveDiscussReport(_this.discussReport).then(res => {
                if (res.Status) {
                    _this.$message.success(res.Message);
                    _this.initData();
                    _this.requestDiscussReports();
                    _this.showDetailsFlag = false;
                }
            }).catch(err => {
                _this.$message.success(',');
            });
        },
        cancel() {
            this.showDetailsFlag = false;
        }

    },
    created() {
        this.requestDiscussReports();
    }
};
</script>


<style scoped>
.mainIEP {
    width: 100%;
    height: 626px;
    position: absolute;
    left: 0;
    top: 0;
}

.discuss-content {
    width: 100%;
    height: 583px;
    position: absolute;
    left: 0;
    top: 0;
    overflow-y: scroll;
}

.center-content {
    width: 1080px;
    height: 571px;
    margin: 0 auto;
}

.colorHeader {
    border-top: 4px solid RGBA(49, 187, 252, 1.00);
    height: 525px;
}

.cardBox {
    width: 100%;
    height: 525px;
}

.tableWrap {
    width: 100%;
    height: 480px;
}

.toolBox {
    float: right;
    margin-right: 55px;
    margin-top: 10px;
    margin-bottom: 10px;
}
</style>



