<template>
    <div class="mainLibrary">
        <div class="lesson-content">

            <div class="lesson-list">
                <div class="toolBox">
                    <div class="addWrap">
                        <el-button @click="addLessonPlan"></el-button>
                    </div>
                </div>

                <div class="center-content">
                    <el-card class="cardBox colorHeader " :body-style="{ padding: '0px'}">
                        <div class="tableWrap ">
                            <el-table :data="lessonPlans" style="width: 100%">
                                <el-table-column type="index" width="160" style="">
                                </el-table-column>

                                <el-table-column prop="Name" label="" width="600">
                                </el-table-column>

                                <!--<el-table-column prop="Name" label="" width="140">
                                                                                                                                                                                    </el-table-column>-->

                                <el-table-column label="">
                                    <template scope="scope">
                                        <!--<el-button size="small" type="danger" @click="assessmentDetail(scope.$index, scope.row)"></el-button>-->
                                        <el-button size="small" :plain="true" type="info" @click="viewLessonPlanDetails(scope.$index,scope.row)"></el-button>
                                        <el-button size="small" :plain="true" type="danger" @click="deleteLessonPlan(scope.$index, scope.row)"></el-button>
                                    </template>
                                </el-table-column>

                            </el-table>
                        </div>
                        <el-pagination layout="prev, pager, next" @current-change="pageChange" :page-size="pageSize" :total="recordsCount" style="float:right;"></el-pagination>
                    </el-card>
                </div>
            </div>

            <el-dialog :title="lessonDialogTitle" v-model="showDetailsFlag" size="small">

                <el-form label-width="140px" :model="lessonPlan">
                    <el-row :gutter="24">
                        <el-col :span="24" class="content">
                            <el-form-item label=":">
                                <el-input v-model="lessonPlan.Name" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="24" class="content">
                            <el-form-item label="/:">
                                <el-input v-model="lessonPlan.LearningContent" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.MainContent">
                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.SecondaryContent">
                                </el-input>
                            </el-form-item>
                        </el-col>

                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-date-picker type="date" placeholder="" v-model="lessonPlan.TeachingDate" style="width:100%"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label="">
                                <el-select v-model="lessonPlan.TeachingType" placeholder="" style="width:100%">
                                    <el-option v-for="(item, index) in TeachingTypes" :key="item.ID" :label=item.Value :value=item.ID>
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.TeachingTarget">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Preparation">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Points">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Difficulties">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Process">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.AbilityLevel">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row :gutter="24">
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Rethink">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" class="content">
                            <el-form-item label=":">
                                <el-input
                                    type="textarea"
                                    autosize
                                    placeholder=""
                                    v-model="lessonPlan.Comments">
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>

                <div style="float:right;margin-bottom:10px;margin-right:25px">
                    <el-button @click="cancel"></el-button>
                    <el-button type="primary" @click="saveLessonPlan"></el-button>
                </div>

            </el-dialog>

        </div>

    </div>
</template>
<script>
import api from '../../../api/index.js';
export default {
    name: 'iepLibrary',
    components: {
    },
    data() {
        return {
            showDetailsFlag: false,
            pageIndex: 1,
            recordsCount: 1,
            pageSize: 10,
            lessonPlans: [],
            lessonPlan: {
                ID: '',
                Name: '',
                LearningContent: '',///
                MainContent: '',
                SecondaryContent: '',
                TeachingDate: '',
                TeachingType: 1,
                AbilityLevel: '',//
                TeachingTarget: '',
                Preparation: '',
                Points: '',
                Difficulties: '',
                Process: '',
                Rethink: '',
                Comments: ''
            },
            TeachingTypes: [{ ID: 1, Value: '' }, { ID: 2, Value: '' }, { ID: 3, Value: '' }],
            lessonDialogTitle: '',
        }
    },
    methods: {
        initLessonPlan() {
            this.lessonPlan = {
                ID: '',
                Name: '',
                LearningContent: '',///
                MainContent: '',
                SecondaryContent: '',
                TeachingDate: '',
                TeachingType: 1,
                AbilityLevel: '',//
                TeachingTarget: '',
                Preparation: '',
                Points: '',
                Difficulties: '',
                Process: '',
                Rethink: '',
                Comments: ''
            };
            this.lessonPlan.TeachingDate = new Date();
        },

        pageChange(currentPage) {
            this.pageIndex = currentPage;
            this.requestLessonPlans();
        },

        viewLessonPlanDetails(index, row) {
            this.initLessonPlan();
            this.lessonPlan = this.lessonPlans[index];
            this.showDetailsFlag = true;
            this.lessonDialogTitle = '';
        },

        deleteLessonPlan(index, row) {
            let _this = this;
            _this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let params = { id: _this.lessonPlans[index].ID };
                api.removeLessonPlan(params).then(res => {
                    if (res.Status) {
                        _this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                    _this.requestLessonPlans();
                }).catch(err => {
                    _this.$message({
                        type: 'error',
                        message: ',!'
                    });
                });
            }).catch(() => {
            });
        },

        //dialog
        addLessonPlan() {
            this.showDetailsFlag = true;
            this.initLessonPlan();
            this.lessonDialogTitle = '';
        },

        //
        requestLessonPlans() {
            let _this = this;
            let params = { PageIndex: _this.pageIndex, PageSize: _this.pageSize };
            api.getLessonPlanList(params).then(res => {
                _this.recordsCount = res.Total;
                _this.lessonPlans = res.Records;
            }).catch(err => {
                _this.$message.error('');
            });
        },

        //
        saveLessonPlan() {
            let _this = this;
            if (_this.lessonPlan.Name === "") {
                _this.$message.warning('');
                return;
            }
            api.saveLessonPlan(_this.lessonPlan).then(res => {
                if (res.Status) {
                    _this.$message.success(res.Message);
                    _this.initLessonPlan();
                    _this.requestLessonPlans();
                    _this.showDetailsFlag = false;
                }
            }).catch(err => {
                _this.$message.success(',');
            });
        },
        cancel() {
            this.showDetailsFlag = false;
        }

    },
    created() {
        this.requestLessonPlans();
    }
};
</script>

<style scoped>
.mainLibrary {
    width: 100%;
    height: 626px;
    position: absolute;
    left: 0;
    top: 0;
}

.lesson-content {
    width: 100%;
    height: 583px;
    position: absolute;
    left: 0;
    top: 0;
    overflow-y: scroll;
}

.center-content {
    width: 1080px;
    height: 571px;
    margin: 0 auto;
}

.colorHeader {
    border-top: 4px solid RGBA(49, 187, 252, 1.00);
    height: 525px;
}

.cardBox {
    width: 100%;
    height: 525px;
}

.tableWrap {
    width: 100%;
    height: 480px;
}

.toolBox {
    float: right;
    margin-right: 55px;
    margin-top: 10px;
    margin-bottom: 10px;
}




.pageNavbar span {
    font-size: 16px;
    cursor: pointer;
    line-height: 42px;
}

.pageNavbar span:hover {
    color: rgb(66, 171, 216);
}

.pageNavbar {
    position: absolute;
    left: -94px;
    top: 0;
    width: 1280px;
    height: 42px;
    border-bottom: 1px solid #cccccc;
}
</style>



